[class*="social__icon"] a {
  display: block; }

[class*="social__icon"] p {
  margin-top: 3px; }

.social__icon-circle {
  margin-top: 40px; }
  .social__icon-circle .icon {
    width: 95px;
    height: 95px;
    position: relative;
    z-index: 1;
    color: #fff;
    font-size: 42px;
    margin-bottom: 17px; }
    .social__icon-circle .icon:before {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: 2; }
    .social__icon-circle .icon > .before {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border-radius: 50%;
      background: linear-gradient(45deg, var(--brk-base-2) 20%, var(--brk-base-5) 70%);
      opacity: 0;
      transition: all .4s ease;
      z-index: 1; }
    .social__icon-circle .icon > .after {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border-radius: 50%;
      background: linear-gradient(45deg, var(--brk-base-3) 20%, var(--brk-secondary-3) 70%);
      z-index: -1; }
  .social__icon-circle:hover .icon > .before {
    opacity: 1; }
  .social__icon-circle-dark .after {
    border-radius: 0 !important;
    background: linear-gradient(to top, rgba(var(--brand-primary-rgb), 0.86) 0%, rgba(var(--secondary-rgb), 0.86) 100%) !important; }
  .social__icon-circle-dark .before {
    border-radius: 0 !important;
    background: linear-gradient(to top, rgba(var(--brand-primary-rgb), 0.86) 0%, rgba(var(--brand-primary-rgb), 0.86) 100%) !important; }

.social__icon-round {
